import React from 'react';
import { Link } from 'gatsby';
import "../styles/subnav.css"
import {useTranslation} from 'react-i18next';
import SubNav from "./subnav"

function Banner(props) {
  const { t, i18n } = useTranslation();

  let backgroundStyles = '#222'
  if(props.primaryColor) {
    if(props.secondaryColor) {
      backgroundStyles = `linear-gradient(to right, ${props.primaryColor}, ${props.secondaryColor})`
    } else {
      backgroundStyles = props.primaryColor
    }
  }

  let options = []
  i18n.options.preload.forEach(iso => {
    options.push({ value: iso, label: t('languages.translations.' + iso )})
  })

  const TheIcon = props.icon

  return (
    <section
        role="banner"
        className={"banner small-text-center " + ((props.bannerClasses) ? props.bannerClasses : ' ') + ((!props.noGradient) ? ' gradient' : '')}
        style={{background: backgroundStyles }}>
        <SubNav />
      { props.image ? <div className="banner-image"><img src={ props.image } /></div> : '' }
      <div className="banner-heading">
        { props.icon ? <TheIcon /> : '' }
        { props.iconImage ? <div className="country-icon"><img src={ props.iconImage } /></div> : '' }
        <h1 className={"small-text-center " + (props.titleClass) ? props.titleClass : ''} itemProp="headline">{props.title}</h1>
        <h2 className="small-text-center" itemProp="description">{props.subtitle}</h2>
      </div>
      <nav role="tablist" className={"centered"}>
        {props.subnav ? props.subnav.map((nav, index) =>
            <Link key={ index } to={ nav.path }>
              { nav.title }
            </Link>
        ) : ''}
      </nav>
    </section>
  )
}

export default Banner
