import React from "react"

import Layout from "../../components/layout"
import SEO from "../../components/seo"
import Banner from '../../components/banner';
import { useTranslation } from 'react-i18next';
import AboutIcon from '../../../assets/menu_icon_about.svg'
import "../../styles/about.css"
import {Link} from 'gatsby';

export default() => {
  const { t, i18n } = useTranslation();

  return (
      <Layout>
        <SEO title={ t('about:about_title') } lang={ i18n.language } />
        <Banner
            title={ t('about:about_title') }
            subtitle={ t('about:about_description') }
            image='https://images.bible.cloud/fab/banners/agencies.jpg'
            icon={AboutIcon}
            subnav={[
              {path: '/about/', title: t('about:about_title')},
              {path: '/about/faq/', title: t('about:faq_title')},
              {path: '/about/contact', title: t('about:contact_title')}
            ]}
        />

        <div id="wrapper" className="row">
          <div className="columns fab-about medium-10 centered">
            <div className="columns fab-cell">
              <div className="cell">
                <img src="https://images.bible.cloud/fab/fab_color_globe.svg"
                     alt="The Find-A-Bible Project" />
                  <h2>The Find-A-Bible Project</h2>
                  <p>Find-A-Bible is a collaborative project of the Forum of
                    Bible Agencies International (FOBAI). Find-A-Bible is built
                    to increase the Bible's accessibility and aims to facilitate
                    access to God's Word in the languages of the world.
                    Find-A-Bible is designed and maintained by the Digital Bible
                    Society (DBS).</p><br />
                  <p>For over 20 years, people have been searching for
                    hard-to-find Bibles on the internet. They often stumble upon
                    the Forum of Bible Agencies International (FOBAI) website
                    and ask for help. Find-A-Bible is inteded to address this
                    growing need. Members share data on available Bibles while
                    FOBAI launched a data-driven website in 2006.</p><br />
                  <p>The past decades have marked unprecedented growth in the
                    Bible cause. Scripture is being translated into new
                    languages at record pace, audio Bibles and Gospel films are
                    multiplying in ways never imagined, and the world continues
                    to search for Bible resources in their own languages.</p>
                  <br />
                    <p>In 2013, FOBAI commissioned a rebuild of Find-A-Bible to
                      the Digital Bible Society. With a grant from BibleLeague
                      of Canada, Find-A-Bible continues to grow. Data has
                      increased 800%. Several completely new data-driven
                      versions have been created. Country and language data is
                      expanding to include missional distribution and
                      translation data.</p><br />
                    <p><a
                        href="http://web.archive.org/web/20120111204527/http://www.forum-intl.net/find_a_bible/"
                        target="_blank">View the original Find-A-Bible 1.0
                      project here!</a></p>
              </div>
              <hr />
            </div>

            <div className="columns fab-cell" id="fobai">
              <div className="cell">
                <img src="https://images.bible.cloud/fab/fab_color_forumn.svg" alt="Find-A-Bible Symposium" />
                  <h2>Find-A-Bible Symposium</h2>

                  <p>The Forum of Bible Agencies International (FOBAI) is an
                    alliance of more than 30 leading international Bible
                    Agencies and Missions Organizations with a shared vision:
                    “working together to maximize the worldwide access and
                    impact of God’s Word.”</p><br />
                  <p>During annual gatherings of the International Bible Forum
                    (FOBAI), friendly people meet to collaborate around
                    Find-A-Bible. Perhaps you would like to join us? Contact us
                    for details, or join us at an upcoming FOBAI annual
                    meeting.</p><br />
                  <p>Check back here or on the FOBAI website for updates about
                    upcoming dates for the Find-A-Bible Symposium.</p><br />
                  <a href="http://www.forum-intl.net/" target="_blank"
                     title="FOBAI Website">
                    <div className="fab-button fab-sheet">FOBAI Website</div>
                  </a>

              </div>
              <hr />
            </div>


            <div className="columns fab-cell" id="helping">
              <div className="cell">
                <h2>We need your help!</h2>
                <img src="https://images.bible.cloud/fab/fab_color_helping.svg"
                     alt="We need your help!" />
                  <p>We really could use your help finding Bibles. If you are an
                    archivist, a librarian, a search enthusiast, and IT techie,
                    or a lover of God's Word, we invite you to join us in the
                    search for world's Bibles. Our aim is to catalog every major
                    edition of every Bible in every language in a way that those
                    Bibles can be discovered, purchased or downloaded and
                    shared.</p><br />
                  <p>Sound like a pretty tall task? It is really not that hard -
                    as most of the work is simply scouring the internet for
                    Bibles and content for the languages with few resources in
                    the Find-A-Bible database - and then sharing that
                    information with us. If this is something that describes you
                    or excites you, please contact us and join us in the search
                    of a lifetime!</p><br />
                  <p>Contact us using the form below and let us know you would
                    like to volunteer. We look forward to connecting with
                    you.</p>
              </div>
              <hr />
            </div>

            <div className="columns fab-cell" id="resources">
              <div className="cell">
                <h2>Adding Resources</h2>
                <img
                    src="https://images.bible.cloud/fab/fab_color_resources.svg"
                    alt="Adding Resources" />
                  <p>Know of a Bible that needs to be in this directory? We
                    would sure like to hear about it. Does your organization
                    have multi-language Bible resources that should be listed
                    here? Let us know! Better yet, why not fill out this form
                    and we'll do what we can to add it to Find-A-Bible.</p><br />
                <Link to="/about/contact">Contact us</Link>

              </div>
              <hr />
            </div>


          </div>
        </div>

      </Layout>
  )
}