import React from 'react';
import { Link } from 'gatsby';
import "../styles/subnav.css"
import {useTranslation} from 'react-i18next';
import Select from 'react-select'
import { MdSearch } from 'react-icons/md'

function SubNav(props) {
  const { t, i18n } = useTranslation();

  const handleChange = selectedOption => {
    i18n.changeLanguage(selectedOption.value)
  }

  let options = []
  i18n.options.preload.forEach(iso => {
    options.push({ value: iso, label: t('languages.translations.' + iso )})
  })

  const customStyles = {
    option: (provided, state) => ({
      ...provided,
      backgroundColor: '#fff',
      color: '#222',
    }),
    control: (provided, state) => ({
      ...provided,
      minHeight: '24px',
      height: '24px',
      marginTop: '4px',
      lineHeight: '10px'
    }),
    placeholder: (provided) => ({
      ...provided,
      height: '24px',
      top: '65%'
    }),
    indicatorsContainer: (provided) => ({
      ...provided,
      width: '24px',
      height: '24px',
      marginRight: '6px'
    }),
    indicatorSeparator: (provided) => ({
      ...provided,
      height:'24px'
    })
  }

  return (
        <div id="sub-nav">
          <div className="breadcrumbs small-hide" itemScope itemType='http://schema.org/BreadcrumbList'>
            {props.breadcrumbs ? props.breadcrumbs.map((nav, index) =>
                <Link itemProp='itemListElement' itemScope itemType='http://schema.org/ListItem' key={index} to={nav.path}>
                  <span itemProp="item">{ nav.title }</span>
                </Link>
            ) : ''}
          </div>
          <Link to="/search" className="search-selector">{ t('search.title') } <MdSearch /></Link>
          <div className="language-selector">
            <Select styles={customStyles} onChange={handleChange} options={options} placeholder={ t('fields.translate') } />
          </div>
        </div>
  )
}

export default SubNav